import useDoc from "@/hooks/useDoc";
import useDocs from "@/hooks/useDocs";
import dayjs from "dayjs";
import { orderBy, Timestamp, where } from "firebase/firestore";
import { IoMdSend } from "react-icons/io";
import { useEffect, useRef, useState } from "react";
import { readMessage, sendMessage } from "@/services/SupportTickets";
import { useSession } from "next-auth/react";
import { useRouter } from "next/router";
import { Button, Input, Spinner } from "@heroui/react";

interface Chat {
  created_at: Timestamp;
  images?: string[];
  text: string;
  user_id: string;
  username: string;
}

const DetailTicket = () => {
  const { data } = useSession();
  const [description, setDescription] = useState("");
  const [loading, setLoading] = useState(false);
  const {
    query: { ticketId },
  } = useRouter();

  const ticket = useDoc(`tickets/${ticketId}`);

  const URMessage = useDocs(
    `tickets/${ticketId}/conversation`,
    null,
    where("user_id", "!=", data?.user?.id),
    where("unread", "==", true)
  );
  const chats: Chat[] = useDocs(
    `tickets/${ticket?.id}/conversation`,
    [],
    orderBy("created_at", "asc")
  ) as Chat[];

  const chatContainerRef = useRef<HTMLDivElement>(null);
  const fecha = (date: any) => {
    if (!date) return "Fecha no disponible";

    const firebaseDate = date?.seconds ? new Date(date.seconds * 1000) : date;
    const newFecha = dayjs(firebaseDate).format("DD/MM/YY h:mm A");
    return newFecha;
  };

  const send = async () => {
    if (description == "") return;

    setLoading(true);
    try {
      setDescription("");
      await sendMessage({
        id: ticketId,
        description,
        user_id: data?.user?.id,
        username: data?.user?.name,
      });
      /*toast.push(
        <Notification title="Mensaje enviado con exito" type="success" />
      );*/
      setLoading(false);
    } catch (error) {
      console.error("fallo al enviar el mensaje", error);
      /*toast.push(
        <Notification title="Error al enviar el mensaje" type="danger" />
      );*/
      setLoading(false);
    }
  };

  useEffect(() => {
    if (chatContainerRef.current) {
      chatContainerRef.current.scrollTop =
        chatContainerRef.current.scrollHeight;
    }
  }, [chats]);

  useEffect(() => {
    const updateReadMessage = async () => {
      await readMessage({ id: ticketId, user_id: data?.user?.id });
    };

    const timeout = setTimeout(() => {
      updateReadMessage();
    }, 5000);

    return () => clearTimeout(timeout);
  }, []);

  if (!ticket || chats.length == 0) return <>Cargando...</>;

  return (
    <div className="flex flex-col gap-4 relative h-full card">
      <div className="flex p-4 justify-between border-b-2 shadow-md">
        <div className="flex flex-col">
          <span className="text-xl font-bold text-primary">{ticket?.topic}</span>
          <span>User: {ticket?.username}</span>
        </div>
        <div className="flex flex-col">
          <div className="flex gap-2">
            <span>Status:</span>
            <span
              className={`${
                ticket?.status == "opened" ? "text-green-500" : "text-red-500"
              }`}
            >
              {ticket?.status == "opened" ? "Open" : "Closed"}
            </span>
          </div>
          <span>{fecha(ticket?.created_at)}</span>
        </div>
      </div>
      <div
        className="flex flex-col gap-4 flex-grow overflow-y-auto h-[500px] p-4"
        ref={chatContainerRef}
      >
        {chats?.map((chat: Chat, index: number) => (
          <div
            className={`p-4 ${
              chat?.user_id == data?.user.id
                ? "bg-blue-200 text-black self-end"
                : "self-start"
            } justify-self-end  rounded-xl border flex flex-col`}
            key={index}
          >
            <div className="flex gap-6">
              <span className="font-bold">{chat?.username}</span>
              <span>{fecha(chat?.created_at)}</span>
            </div>
            <span>{chat?.text}</span>
          </div>
        ))}
      </div>
      {URMessage && URMessage.length > 0 && (
        <div className="flex p-4 justify-center">
          {URMessage && URMessage?.length} Mensaje
          {URMessage?.length == 0 ? "" : "s"} no leído
          {URMessage?.length == 0 ? "" : "s"}
        </div>
      )}
      <div className="flex gap-4 p-4">
        <div className="w-full">
          <Input
            type="text"
            size="lg"
            placeholder="Escribe aqui"
            value={description}
            disabled={loading}
            onChange={(e) => setDescription(e.target.value)}
            onKeyDown={(e) => {
              if (e.key === "Enter") {
                e.preventDefault();
                send();
              }
            }}
          />
        </div>
        <div className="h-full">
          <Button
            className="py-6 flex items-center text-xl"
            isLoading={loading}
            onPress={send}
          >
            <IoMdSend />
          </Button>
        </div>
      </div>
    </div>
  );
};

export default DetailTicket;
